$background-color1: rgba(221, 221, 221, 0.9);
$background-color1-1: rgba(221, 221, 221, 0.7);
$background-color2: rgba(51, 51, 51, 0.9);
$background-color2-2: rgba(51, 51, 51, 0.7);
$background-theme: #000;
$font-color1: #333;
$font-color2: #ddd;
$font-color3: #666;

@mixin horizontal {
  @media (orientation: landscape) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: 1024px) {
    @content;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
  font-family: "Roboto", sans-serif;
  height: 100vh;
  height: -webkit-fill-available;
  background-color: $background-theme;
}

body {
  background: linear-gradient(90deg,
      rgba(0, 0, 0, 0.8) 0%,
      rgba(0, 0, 0, 1) 50%);
}

@import "App";
@import "Background";
@import "Head";
@import "Section";
@import "Card";
@import "Contact";
@import "Navbar";